* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.list {
  padding: 0;
  list-style: none;
  display: flex;
}

.item {
  margin-left: 20px;
}

.item:first-child {
  margin-left: 0;
}

.link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
